

.profile{
    display: flex;
}

.profileRight{
    flex: 9;
}

.profileCover{
    height: 320px;
    position: relative;
}

.profileCoverImg{
    width: 100%;
    height: 250px;
    object-fit: cover;
}

.profileUserImg{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: 150px;
    border: 3px solid white;
}

.profileInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.profileInfoName{
    font-size: 24px;
}

.profileInfoDesc{
    font-weight: 300;
    text-align: center;
}

.profileRightBottom{
    display: flex;
}
.edit img{
    width: 25px;
}
.edit{
    /* width: 25px; */
    border: 2px solid black;
    border-radius: 50%;
    margin: 2px;
    padding: 5px;
    
}
@media screen and (max-width:1160px) {
    .eimge{
    position: absolute;
    top: 8px;
    right: 10px;
    left: 80%;
    }
}